  <script>
import axios from "axios";
import { Button, Toast, Notify } from "vant";
export default {
  data() {
    return {
      phone: "",
      password: "",
      yzm: "",
    };
  },
  name: "login",
  components: {
    [Button.name]: Button,
    [Notify.name]: Notify,
  },
  methods: {
    back(){
      this.$router.go(-1)
    },
    getyzm() {
      if (this.phone.length === 11) {
        axios({
          method: "post",
          url: "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/sendSms",
          data: {
            mobile: this.phone,
            type: "register",
          },
        }).then((res) => {
          console.log(res.data);
          Toast(res.data.msg);
          if(res.data.msg === '账号已经存在'){
            Notify({
              type: "warning",
              message: `${res.data.msg},请登录`,
              duration: 2000,
            });
          }else{
            Notify({
              type: "primary",
              message: `你的验证码是:${res.data.data}`,
              duration: 5000,
            });
          }
          
        });
      } else {
        Notify({
          type: "warning",
          message: `请输入正确的手机号`,
          duration: 2000,
        });
      }
    },
    zhuce() {
      axios({
        method: "post",
        url: "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/reg",
        data: {
          username: this.phone,
          password: this.password,
          vercode: this.yzm,
        },
      }).then((res) => {
        console.log(res);
        if(res.data.msg ==='注册成功'){
          Notify({
              type: "primary",
              message: `注册成功，请登录`,
              duration: 2000,
            });
          this.$router.push("/login");
        }
        else{
          Notify({
              type: "warning",
              message: `请先注册`,
              duration: 1000,
            });
        }
      });
    },
  },
};
</script>
  
